"use server";

import fs from "fs";
import path from "path";
import { v4 as uuidv4 } from "uuid";
import { getAllStudios } from "@/lib/db-studio";

const CAMPAIGNS_DB_PATH = path.join(process.cwd(), "data", "campaigns.json");

// Initialize campaigns DB
if (!fs.existsSync(CAMPAIGNS_DB_PATH)) {
    fs.writeFileSync(CAMPAIGNS_DB_PATH, JSON.stringify([], null, 2));
}

interface Campaign {
    id: string;
    title: string;
    description: string;
    discountType: "percentage" | "fixed";
    discountValue: number;
    target: "all" | "specific";
    targetStudioIds: string[];
    startDate: string;
    endDate: string;
    isActive: boolean;
    createdAt: string;
}

// Get all campaigns
export async function fetchCampaigns(): Promise<Campaign[]> {
    try {
        const data = fs.readFileSync(CAMPAIGNS_DB_PATH, "utf-8");
        return JSON.parse(data);
    } catch (error) {
        return [];
    }
}

// Get studios (for target selection)
export async function fetchStudios() {
    return getAllStudios();
}

// Create new campaign
export async function createCampaign(data: {
    title: string;
    description: string;
    discountType: "percentage" | "fixed";
    discountValue: number;
    target: "all" | "specific";
    targetStudioIds: string[];
    startDate: string;
    endDate: string;
    isActive: boolean;
}): Promise<{ success: boolean; campaignId: string }> {
    const campaigns = await fetchCampaigns();

    const newCampaign: Campaign = {
        id: uuidv4(),
        ...data,
        createdAt: new Date().toISOString(),
    };

    campaigns.push(newCampaign);
    fs.writeFileSync(CAMPAIGNS_DB_PATH, JSON.stringify(campaigns, null, 2));

    return { success: true, campaignId: newCampaign.id };
}

// Update campaign
export async function updateCampaign(
    data: Campaign
): Promise<{ success: boolean }> {
    const campaigns = await fetchCampaigns();
    const campaignIndex = campaigns.findIndex((c) => c.id === data.id);

    if (campaignIndex === -1) {
        return { success: false };
    }

    campaigns[campaignIndex] = data;
    fs.writeFileSync(CAMPAIGNS_DB_PATH, JSON.stringify(campaigns, null, 2));

    return { success: true };
}

// Delete campaign
export async function deleteCampaign(id: string): Promise<{ success: boolean }> {
    const campaigns = await fetchCampaigns();
    const filteredCampaigns = campaigns.filter((c) => c.id !== id);

    fs.writeFileSync(CAMPAIGNS_DB_PATH, JSON.stringify(filteredCampaigns, null, 2));

    return { success: true };
}

// Get active campaigns for a specific studio
export async function getActiveCampaignsForStudio(
    studioId: string
): Promise<Campaign[]> {
    const campaigns = await fetchCampaigns();
    const today = new Date().toISOString().split("T")[0];

    return campaigns.filter(
        (campaign) =>
            campaign.isActive &&
            campaign.startDate <= today &&
            campaign.endDate >= today &&
            (campaign.target === "all" ||
                campaign.targetStudioIds.includes(studioId))
    );
}
